import { NextResponse } from "next/server";
import prisma from "@/lib/prisma";
import { getUserFromCookie } from "@/lib/auth";
import formidable from "formidable-serverless";
import fs from "fs";
import path from "path";

// Directory to store uploaded files
const uploadDir = path.join(process.cwd(), "public/uploads");
if (!fs.existsSync(uploadDir)) fs.mkdirSync(uploadDir, { recursive: true });

// GET post by ID
export async function GET(req, context) {
  const params = await context.params; // <-- unwrap params
  try {
    const post = await prisma.post.findUnique({
      where: { id: params.id },
    });

    if (!post) {
      return NextResponse.json({ error: "Post not found" }, { status: 404 });
    }

    return NextResponse.json({ post });
  } catch (err) {
    console.error("GET post error:", err);
    return NextResponse.json({ error: "Internal Server Error" }, { status: 500 });
  }
}

// PUT post by ID (edit)
export async function PUT(req, context) {
  const params = await context.params; // <-- unwrap params
  const user = await getUserFromCookie(req);
  if (!user) return NextResponse.json({ error: "Unauthorized" }, { status: 401 });

  const form = formidable({
    multiples: false,
    keepExtensions: true,
    uploadDir,
  });

  try {
    const data = await new Promise((resolve, reject) => {
      form.parse(req, (err, fields, files) => {
        if (err) reject(err);
        else resolve({ fields, files });
      });
    });

    const { title, content } = data.fields;
    const imageFile = data.files?.image;
    const videoFile = data.files?.video;

    const post = await prisma.post.findUnique({ where: { id: params.id } });
    if (!post) return NextResponse.json({ error: "Post not found" }, { status: 404 });
    if (post.authorId !== user.id) return NextResponse.json({ error: "Forbidden" }, { status: 403 });

    const imageUrl = imageFile ? `/uploads/${path.basename(imageFile.filepath)}` : post.image;
    const videoUrl = videoFile ? `/uploads/${path.basename(videoFile.filepath)}` : post.video;

    const updated = await prisma.post.update({
      where: { id: params.id },
      data: { title, content, image: imageUrl, video: videoUrl },
    });

    return NextResponse.json({ post: updated });
  } catch (err) {
    console.error("PUT post error:", err);
    return NextResponse.json({ error: "Failed to update post" }, { status: 500 });
  }
}

// DELETE post by ID
export async function DELETE(req, context) {
  const params = await context.params; // <-- unwrap params
  const user = await getUserFromCookie(req);
  if (!user) return NextResponse.json({ error: "Unauthorized" }, { status: 401 });

  try {
    const post = await prisma.post.findUnique({ where: { id: params.id } });
    if (!post) return NextResponse.json({ error: "Post not found" }, { status: 404 });
    if (post.authorId !== user.id) return NextResponse.json({ error: "Forbidden" }, { status: 403 });

    await prisma.post.delete({ where: { id: params.id } });
    return NextResponse.json({ success: true });
  } catch (err) {
    console.error("DELETE post error:", err);
    return NextResponse.json({ error: "Failed to delete post" }, { status: 500 });
  }
}
